@use "reset";
@use "variable" as var;
@use "mixin" as mx;
@use "loading";
@use "btn";
@use "swiper";

html{scroll-behavior: smooth;}

body{
    width: 100%;
    min-height: 100%;
    background-color: #fff;
    font-size: 16px;
    color: var.$color-key;
    font-family: "Inter",
    "Helvetica Neue",
    Arial,
    "Hiragino Kaku Gothic ProN",
    "Hiragino Sans",
    "BIZ UDPGothic",
    Meiryo,
    sans-serif;
    letter-spacing: 0.1ex;

}
img{
    width: 100%;
    object-fit: contain;
}
.container{
    position: relative;
    top: 0;
    z-index: 1;
}
.side-border{
    position: fixed;
    z-index: -1;
    top: 0;
    width: 100%;
    height: 100lvh;
    &_left{
        display: block;
        position: absolute;
        top: 0;
        left: 40px;
        width: 1px;
        height: 100vh;
        background-color: var.$color-gray;
        @include mx.mq(sp){
            left: 20px;
        }
    }
    &_right{
        display: block;
        position: absolute;
        top: 0;
        right: 40px;
        width: 1px;
        height: 100vh;
        background-color: var.$color-gray;
        @include mx.mq(sp){
            right: 20px;
        }
    }
}
@keyframes fadeIn {
    from{
        visibility: hidden;
        opacity: 0;
        transform: scale(0.9);
        filter: blur(0.8px);
    }
    to{
        visibility: visible;
        opacity: 1;
        transform: scale(1);
        filter: blur(0px);
    }
}
@keyframes fadeOut {
    from{
        visibility: visible;
        opacity: 1;
        transform: scale(1);
        filter: blur(0px);
    }
    to{
        visibility: hidden;
        opacity: 0;
        transform: scale(0.9);
        filter: blur(0.8px);
    }
}
.header{
    position: fixed;
    top: 0;
    z-index: 1;
    width: 100%;
    height: auto;
    background: transparent;
    border-top: 1px solid var.$color-key;
    border-bottom: 1px solid var.$color-key;
    transition: filter ease 0.3s, background ease 0.3s;
    &.filter-on{
        background: rgba($color: #fff, $alpha: 0.2);
        filter: invert(1);
        .header-navi{
            @include mx.mq(sp){
                background-color: rgba($color: #fff, $alpha: 0.7);
            }
        }
    }
    &.filter-off{
        background: rgba($color: #fff, $alpha: 1.0);
        filter: invert(0);
    }
    &-inner{
        position: relative;
        margin: 0 40px;
        border-right: 1px solid var.$color-key;
        border-left: 1px solid var.$color-key;
        @include mx.mq(sp){
            margin: 0 20px;
            padding: 24px;
        }
    }
    &-column{
        display: flex;
        justify-content: space-between;
        align-items: center;
        @include mx.mq(sp){
            flex-direction: column;
            justify-content: flex-start;
            align-items: flex-start;
        }
    }
    &-logo{
        width: 242px;
        padding: 0 0 0 40px;
        @include mx.mq(sp){
            width: 160px;
            padding: unset;
        }
        svg{
            width: 100%;
        }
        
    }
    &-toggle{
        display: none;
        @include mx.mq(sp){
            display: flex;
            flex-direction: column;
            justify-content: space-between;
            position: absolute;
            top: 0;
            bottom: 0;
            right: 16px;
            margin: auto;
            width: 67px;
            height: 7px;
            cursor: pointer;
            & > span{
                display: block;
                height: 1px;
                background: var.$color-key;
                transition: transform 0.3s ease 0s;
            }
        }
    }
    &-navi{
        position: relative;
        display: flex;
        justify-content: flex-start;
        @include mx.mq(sp){
            visibility: hidden;
            position: fixed;
            z-index: 3;
            top: 70px;
            width: 100%;
            height: 100lvh;
            left: 0;
            right: 0;
            margin: auto;
            flex-direction: column;
            background: #fff;
            &::before{
                content: "";
                display: block;
                position: absolute;
                top: 0;
                left: 20px;
                width: 1px;
                height: 100vh;
                background-color: var.$color-key;
            }
            &::after{
                content: "";
                display: block;
                position: absolute;
                top: 0;
                right: 20px;
                width: 1px;
                height: 100vh;
                background-color: var.$color-key;
            }
        }
        &.open{
            @include mx.mq(sp){
                animation: fadeIn 0.3s ease 0s forwards;
            }
        }
        &.close{
            @include mx.mq(sp){
                animation: fadeOut 0.3s ease 0s forwards;
            }
        }
    }
    &-sns{
        display: grid;
        place-content: center;
        border-left: solid 1px var.$color-key;
        width: 106px;
        margin: 0 0 0 40px;
        @include mx.mq(sp){
            width: 89%;
            margin: 40px auto;
            padding: 24px 20px;
            border-top: solid 1px var.$color-key;
            border-bottom: solid 1px var.$color-key;
            border-left: none;
        }
        a{
            display: block;
            width: 28px;
            height: 28px;
            &:hover{
                opacity: 0.7;
            }
        }
    }
}
#nav-input{
    display: none;
    // &:checked ~ .header-navi{
    //     visibility: visible;
    // }
    &:checked + .header-toggle{
        span{
            &:first-child{
                transform: translateY(3px) rotate(14deg);
            }
            &:last-child{
                transform: translateY(-3px) rotate(-14deg);
            }
        }
    }
}


.navi{
    display: flex;
    gap: 40px;
    padding: 24px;
    @include mx.mq(sp){
        flex-direction: column;
        justify-content: center;
    }
    &-item{
        text-align: center;
        position: relative;
        a{
            display: block;
            opacity: 1;
            transition: opacity 0.3s ease;
            &::before{
                content: "";
                position: absolute;
                left: 0;
                right: 0;
                bottom: -16px;
                margin: auto;
                display: block;
                width: 10px;
                height: 10px;
                border-radius: 50%;
                transform: scale(0);
                background-color: var.$color-key;
                transition: transform 0.3s ease;
            }
            &:hover{
                opacity: 0.7;
                &::before{
                    transform: scale(1);
                }
            }
        }
        &_en{
            margin: 0 0 4px 0;
            font-size: 18px;
        }
        &_ja{
            font-size: 14px;
        }
    }
}
.main-visual{
    position: relative;
    z-index: 0;
    width: 100%;
    aspect-ratio: 16/9;
    margin: 0 auto 154px;
    @include mx.mq(sp){
        margin: 0px auto 100px;
        aspect-ratio: 2/3;
    }
    &_photo{
        transform: scale(1.0);
    }
    &_logo{
        position: absolute;
        z-index: 2;
        inset: 0;
        margin: auto;
        max-width: 640px;
        height: fit-content;
        @include mx.mq(sp){
            width: calc(208px / 320px * 100%);
        }
    }
}

.section{
    &-titles{
        width: fit-content;
        opacity: 0;
        transition: opacity 0.5s ease 0.8s;
        &_head{
            font-weight: 600;
            font-size: 56px;
            @include mx.mq(sp){
                font-size: 24px;
            }
            color: #fff;
            text-shadow: 0px 0px 5px rgba($color: #000000, $alpha: 0.4);
        }
        &_sub{
            font-size: 32px;
            @include mx.mq(sp){
                font-size: 16px;
            }
            font-weight: bold;
            color: #fff;
            letter-spacing: .3ex;
            text-shadow: 0px 0px 5px rgba($color: #000000, $alpha: 0.4);
        }
        &.active{
          opacity: 1;  
        }
    }
    &-text{
        font-size: 16px;
        line-height: 1.7;
        margin: 0 0 32px 0;
        @include mx.mq(sp){
            font-size: 14px;
        }
        br.sp-clear{
            display: block;
            @include mx.mq(sp){
                display: none;
            }
        }
    }
    &-list{
        padding: 0 0 0 40px;
        margin: 0 0 32px 0;
        border-left: 1px solid var.$color-gray;
        font-size: 16px;
        @include mx.mq(sp){
            padding: 0 0 0 20px;
            font-size: 14px;
        }
        &_item{
            margin: 0 0 8px 0;
            a{
                color: #0a1e47;
                text-decoration: underline;
                &:hover{
                    color: var.$color-gray;
                }
            }
        }
    }
    &-detail{
        &_title{
            font-size: 24px;
            margin: 0 0 24px 0;
            font-weight: bold;
            @include mx.mq(sp){
                font-size: 16px;
            }
        }
        &_text{
            font-size: 16px;
            padding: 0 0 8px 40px;
            @include mx.mq(sp){
                font-size: 14px;
                padding: 0 0 8px 20px;
            }
            border-left: 1px solid var.$color-gray;
        }
    }
    &-thumbs{
        position: relative;
        top: 0;
        overflow: hidden;
        &::before{
            content: "";
            display: block;
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            background: #fff;
            transition: left 0.8s ease 0s, width 1s ease 0.5s,;
        }
        &.active{
            &::before{
                width: 0%;
                left:100%;
            }
        }
    }
}
.business{
    max-width: 1280px;
    margin: 0 auto 156px;
    @include mx.mq(sp){
        margin: 0 auto 100px;
    }
    &-thumbs{
        position: relative;
        top: 0;
        width: 960px;
        margin: 0 auto 64px;
        @include mx.mq(sp){
            width: 80%;
            margin: 0 0 32px 20px;
        }
    }
    &-titles{
        position: absolute;
        bottom: 40px;
        left: 40px;
        @include mx.mq(sp){
            bottom: 20px;
            left: 20px;
        }
    }
    &-details{
        width: 960px;
        margin: 0 auto;
        padding: 0 40px 0 40px;
        @include mx.mq(sp){
            width: 100%;
        }
    }
}
.mechannical{
    margin: 0 80px 156px 80px;
    @include mx.mq(sp){
        margin: 0px 20px 80px 40px;
    }
    &-column{
        display: flex;
        align-items: center;
        gap: 80px;
        @include mx.mq(sp){
            gap: 32px;
            flex-direction: column;
        }
        &_thumb{
            flex-basis: 60%;
            position: relative;
            top: 0;
            aspect-ratio: 16 / 9;
        }
        &_titles{
            position: absolute;
            bottom: 40px;
            left: 40px;
            @include mx.mq(sp){
                bottom: 20px;
                left: 20px;
            }
        }
    }
}
.recruit{
    margin: 0 80px 156px 80px;
    @include mx.mq(sp){
        margin: 0 40px 100px 20px;
    }
    &-column{
        display: flex;
        flex-direction: row-reverse;
        gap: 80px;
        @include mx.mq(sp){
            gap: 32px;
            flex-direction: column;
        }
        &_thumb{
            flex-basis: 60%;
            position: relative;
            top: 0;
        }
    }
    &-titles{
        position: absolute;
        top: 40px;
        left: 40px;
        @include mx.mq(sp){
            top: 20px;
            left: 20px;
        }
    }
    &-details{
        @include mx.mq(sp){
            margin: 0 0 0 20px;
        }
    }
}
.footer{
    border-top: 1px solid var.$color-key;
    border-bottom: 1px solid var.$color-key;
    background: #fff;
    &-inner{
        margin: 0 40px;
        border-right: 1px solid var.$color-key;
        border-left: 1px solid var.$color-key;
        @include mx.mq(sp){
            margin: 0 20px;
        }
    }
    &-column{
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        grid-column-gap: 0px;
        grid-row-gap: 0px;
        border-bottom: 1px solid var.$color-key;
        @include mx.mq(sp){
            grid-template-columns: 1fr;
            grid-template-rows: repeat(2, auto);
        }
        &:last-child{
            border-bottom: none;
        }
    }
    &-company{
        padding: 32px 0  56px 32px;
        border-right: 1px solid var.$color-key;
        @include mx.mq(sp){
            padding: 32px 20px  56px 20px;
            border-right: none;
            border-bottom: 1px solid var.$color-key;
        }
    }
    &-maps{
        position: relative;
        top: 0;
        @include mx.mq(sp){
            height: 360px;
        }
        iframe{
            position: absolute;
            top: 0;
            width: 99.99%;
            height: 100%;
        }
    }
    &-detail{
        display: flex;
        flex-wrap: wrap;
        row-gap: 16px;
        @include mx.mq(sp){
            flex-direction: column;
            row-gap: 0px;
        }
        &_title{
            flex-basis: 25%;
            font-size: 16px;
            font-weight: 600;
            @include mx.mq(sp){
                font-size: 14px;
                margin: 0 0 8px 0;
            }
            
        }
        &_content{
            flex-basis: 70%;
            font-size: 16px;
            @include mx.mq(sp){
                font-size: 14px;
                margin: 0 0 16px 0;
            }
        }
    }
    &-contact{
        display: flex;
        align-items: center;
        gap: 56px;
        padding: 32px;
        border-right: 1px solid var.$color-key;
        @include mx.mq(sp){
            border-right: none;
            gap: 16px;
            padding: 24px 0;
            flex-direction: column;
            border-bottom: 1px solid var.$color-key;
        }
        &_heading{
            font-size: 32px;
            font-weight: 600;
        }
    }
    &-copy{
        display: flex;
        justify-content: center;
        align-items: center;
        small{
            margin: 0 24px 0;
            font-size: 14px;
            @include mx.mq(sp){
                font-size: 14px;
                padding: 24px 0;
            }
        }
        img{
            display: block;
            width: 70px;
            object-fit: cover;
            @include mx.mq(sp){
                width: 60px;
            }
        }
    }
}
.company{
    &-head{
        color: var.$color-key;
        text-shadow: none;
        font-size: 48px;
        @include mx.mq(sp){
            font-size: 32px;
        }
    }
    &-sub{
        color: var.$color-key;
        text-shadow: none;
        margin: 0 0 32px 0;
    }
}
#defaultCanvas0{
    @include mx.mq(sp){
        display: none;
    }
}